import * as React from "react";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import DeleteIcon from "@mui/icons-material/Delete";
import EditIcon from "@mui/icons-material/Edit";
import CheckCircleOutlineIcon from "@mui/icons-material/CheckCircleOutline";
import BlockIcon from "@mui/icons-material/Block";

function createData(
  name: string,
  admin_user: boolean,
  admin_office: boolean,
  admin_category: boolean
) {
  return { name, admin_user, admin_office, admin_category };
}

const rows = [
  createData("Administrador Geral", true, true, true),
  createData("Gerente de projetos", true, true, false),
  createData("Usurio Comum", false, false, false),
];

export default function OfficeTable() {
  return (
    <TableContainer component={Paper}>
      <Table sx={{ minWidth: 650 }} size="small" aria-label="a dense table">
        <TableHead>
          <TableRow>
            <TableCell align="center" style={{ fontWeight: "Bold" }}>
              Nome
            </TableCell>
            <TableCell align="center" style={{ fontWeight: "Bold" }}>
              Gerenciamento de usurios
            </TableCell>
            <TableCell align="center" style={{ fontWeight: "Bold" }}>
              Gerenciamento de Cargos
            </TableCell>
            <TableCell align="center" style={{ fontWeight: "Bold" }}>
              Gerenciamento de Categorias
            </TableCell>
            <TableCell align="center" style={{ fontWeight: "Bold" }}>
              Aes
            </TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow
              key={row.name}
              sx={{ "&:last-child td, &:last-child th": { border: 0 } }}
            >
              <TableCell align="center">{row.name}</TableCell>
              <TableCell align="center">
                {row.admin_user ? (
                  <CheckCircleOutlineIcon style={{ color: "green" }} />
                ) : (
                  <BlockIcon style={{ color: "red" }}/>
                )}
              </TableCell>
              <TableCell align="center">
                {row.admin_office ? <CheckCircleOutlineIcon style={{ color: "green" }} /> : <BlockIcon style={{ color: "red" }}/>}
              </TableCell>
              <TableCell align="center">
                {row.admin_category ? (
                  <CheckCircleOutlineIcon style={{ color: "green" }} />
                ) : (
                  <BlockIcon style={{ color: "red" }}/>
                )}
              </TableCell>
              <TableCell align="center">
                <DeleteIcon style={{ cursor: "pointer" }} />
                <EditIcon style={{ cursor: "pointer" }} />
              </TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
