import * as React from "react";
import Button from "@mui/material/Button";
import AddIcon from "@mui/icons-material/Add";
import SimpleDialog from "../../../shared/components/dialog/SimpleDialog";
import TextField from "@mui/material/TextField";
import Grid from "@mui/material/Grid";
import Checkbox from "@mui/material/Checkbox";
import FormControlLabel from "@mui/material/FormControlLabel";

export default function CreateOfficeDialog() {
  const [CreateOfficeDialogIsOpen, setCreateOfficeDialogIsOpen] =
    React.useState(false);

  const handleCreateOfficeDialog = () => {
    setCreateOfficeDialogIsOpen(!CreateOfficeDialogIsOpen);
  };

  return (
    <>
      <Button
        startIcon={<AddIcon />}
        variant="contained"
        style={{
          marginBottom: "20px",
          marginTop: "20px",
          backgroundColor: "#00FFAC",
          color: "Black",
        }}
        onClick={handleCreateOfficeDialog}
      >
        Criar novo cargo
      </Button>
      <SimpleDialog
        title="Criao de cargo"
        open={CreateOfficeDialogIsOpen}
        handleClose={handleCreateOfficeDialog}
      >
        <div style={{ marginTop: "15px" }}>
          <Grid container>
            <Grid xs={6}>
              <p>Nome do cargo</p>
            </Grid>
            <Grid xs={6}>
              <TextField
                id="outlined-basic"
                variant="outlined"
                style={{ width: "100%" }}
                type="text"
              />
            </Grid>
          </Grid>
        </div>
        <div style={{ marginTop: "15px" }}>
            <div style={{ width: "50%" }}>
              <FormControlLabel
                control={<Checkbox />}
                label="Administrar Usrios"
              />
              <FormControlLabel
                control={<Checkbox />}
                label="Administrar Cargos"
              />
              <FormControlLabel
                control={<Checkbox />}
                label="Administrar Stacks"
              />
            </div>
        </div>
      </SimpleDialog>
    </>
  );
}
