import * as React from "react";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";
import Link from "@mui/material/Link";
import Box from "@mui/material/Box";
import Grid from "@mui/material/Grid";
import { createTheme } from "@mui/material/styles";

const theme = createTheme();

export default function Register({ setPageSelected }: any) {
  const [formData, setFormData] = React.useState({
    email: "",
    password: "",
    confirmation: "",
    name: "",
    telephone: "",
  });

  return (
    <Box component="form" noValidate sx={{ mt: 1 }}>
      <TextField
        margin="normal"
        required
        fullWidth
        label="Nome"
        onChange={(e) => setFormData({ ...formData, name: e.target.value })}
        autoFocus
      />
      <TextField
        margin="normal"
        required
        fullWidth
        label="Email"
        autoComplete="email"
        onChange={(e) => setFormData({ ...formData, email: e.target.value })}
        autoFocus
      />
      <TextField
        margin="normal"
        required
        fullWidth
        label="Telefone"
        onChange={(e) => setFormData({ ...formData, telephone: e.target.value })}
        autoFocus
      />
      <TextField
        margin="normal"
        required
        fullWidth
        name="password"
        label="Senha"
        type="password"
        id="password"
        onChange={(e) =>
          setFormData({ ...formData, password: e.target.value })
        }
        autoComplete="current-password"
      />
       <TextField
        margin="normal"
        required
        fullWidth
        name="password"
        label="Confirmar Senha"
        type="password"
        id="password"
        onChange={(e) =>
          setFormData({ ...formData, password: e.target.value })
        }
        autoComplete="current-password"
      />
      <Button style={{ backgroundColor: "#00FFAC", color: "Black" }} type="submit" fullWidth variant="contained" sx={{ mt: 3, mb: 2 }}>
        Finalizar Cadastro
      </Button>
      <Button
        onClick={() => setPageSelected("Login")}
        fullWidth
        variant="contained"
        sx={{ mt: 1 }}
        style={{ backgroundColor: "#00FFAC", color: "Black" }}
      >
        Voltar ao inicio
      </Button>
    </Box>
  );
}
