import * as React from "react";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import Box from "@mui/material/Box";
import { createTheme } from "@mui/material/styles";

const theme = createTheme();

export default function Recover({ setPageSelected }: any) {
  const [credentials, setCredentials] = React.useState({
    email: "",
  });

  return (
    <Box component="form" noValidate sx={{ mt: 1 }}>
      <TextField
        margin="normal"
        required
        fullWidth
        label="Email"
        autoComplete="email"
        onChange={(e) => setCredentials({ email: e.target.value })}
        autoFocus
      />
      <Button
        style={{ backgroundColor: "#00FFAC", color: "Black" }}
        type="submit"
        fullWidth
        variant="contained"
        sx={{ mt: 3, mb: 2 }}
      >
        Recuperar conta
      </Button>
      <Button
        onClick={() => setPageSelected("Login")}
        fullWidth
        variant="contained"
        sx={{ mt: 1 }}
        style={{ backgroundColor: "#00FFAC", color: "Black" }}
      >
        Voltar ao inicio
      </Button>
    </Box>
  );
}
